/** @jsx jsx */
import * as React from "react"
import { Box, ThemeProvider, Theme, ThemeUIStyleObject, jsx } from "theme-ui"
import { Global } from "@emotion/react"
import CookieConsent from "react-cookie-consent"
import SEO from "./seo"

type LayoutProps = {
  children: React.ReactNode, className?: string, theme: Theme, sx?: ThemeUIStyleObject
}

const Layout = ({children, className = ``, theme, sx = undefined }: LayoutProps) => (
  <>
    <ThemeProvider theme={theme}>
      <Global
        styles={(theme) => ({
          "*": {
            boxSizing: `inherit`,
            "&:before": {
              boxSizing: `inherit`,
            },
            "&:after": {
              boxSizing: `inherit`,
            },
          },
          html: {
            fontSize: `18px`,
            WebkitTextSizeAdjust: `100%`,
          },
          img: {
            borderStyle: `none`,
          },
          pre: {
            fontFamily: `monospace`,
            fontSize: `1em`,
          },
          "[hidden]": {
            display: `none`,
          },
        })}
      />
      <SEO />
      <Box sx={sx}>
        <main className={className}>{children}</main>
      </Box>
      <CookieConsent
        location="bottom"
        buttonText="I understand"
        declineButtonText="Decline"
        cookieName="gdpr-cookie-consent"
      >
        We use essential cookies on our website.
        We don't use cookies to track you or collect information about you.<br/>
        <span style={{ fontSize: "80%" }}>
          For more information see our <a href="/privacy">privacy policy</a>.
        </span>
      </CookieConsent>
    </ThemeProvider>
  </>
)

export default Layout
